@Composable
fun ProductsPage() {

    val viewModel = remember { GlobalContext.get().get<ProductsViewModel>() }
    val state: Lce<Exception, ProductsUiState> by viewModel.state.collectAsState()
    val onEvent = viewModel::onEvent
    val ctx = rememberPageContext()

    DisposableEffect(Unit) {
        onDispose {
            viewModel.onDispose()
        }
    }

    LaunchedEffect(Unit) {
        onEvent(ProductsUiEvent.Init)
    }

    LaunchedEffect(Unit) {
        viewModel.navigationEffect.collect { effect: ProductsNavigationEffect ->
            console.log("Log nav effect $effect")
            when (effect) {
                is ProductsNavigationEffect.NavigateToDetail -> {
                    console.log("Navigate to /products/${effect.productId}")
                    ctx.router.navigateTo("/products/${effect.productId}")
                }
            }
        }
    }

    Column(
        modifier = Modifier.fillMaxWidth().gap(2.cssRem),
        horizontalAlignment = Alignment.CenterHorizontally
    ) {
        val stringRes = LocalStrings.current
        // Page Title
        SpanText(
            stringRes.ourProducts,
            Modifier
                .fontSize(3.cssRem)
                .fontWeight(FontWeight.Bold)
                .color(ColorMode.current.toSitePalette().brand.primary)
        )

        when (state) {
            is Lce.Content<*> -> DisplayContent(state, onEvent)
            is Lce.Failure<*> -> TODO()
            Lce.Loading -> SpanText(
                stringRes.loading,
                Modifier
                    .fontSize(3.cssRem)
                    .fontWeight(FontWeight.Bold)
                    .color(ColorMode.current.toSitePalette().brand.primary)
            )
        }

    }
}

@Composable
private fun DisplayContent(
    state: Lce<Exception, ProductsUiState>,
    onEvent: (ProductsUiEvent) -> Unit
) {
    (state as Lce.Content<ProductsUiState>).value.takeIf { it.items.isNotEmpty() }?.let {
        // Products Grid
        Div(ProductsGridStyle.toModifier().toAttrs()) {
            it.items.forEach { product ->
                ProductCard(product, onEvent)
            }
        }
    }
}

val ProductCardStyle = CssStyle.base {
    Modifier
        .borderRadius(1.cssRem)
        .padding(1.5.cssRem)
//        .backgroundColor(background)
        .boxShadow(
            offsetX = 0.px,
            offsetY = 4.px,
            blurRadius = 6.px,
            color = org.jetbrains.compose.web.css.rgba(0, 0, 0, 0.1)
        )
}


@Composable
private fun ProductCard(product: ProductItemElement, onEvent: (ProductsUiEvent) -> Unit) {
    val background = ColorMode.current.toSitePalette().nearBackground

    Column(
        modifier = ProductCardStyle.toModifier()
            .background(background)
            .cursor(Cursor.Pointer)
            .onClick {
                console.log("Lauched event ${ProductsUiEvent.NavigateToProductDetail(product.id)}")
                onEvent(ProductsUiEvent.NavigateToProductDetail(product.id))
            }
            .transition(
                Transition.of(
                    property = "transform",
                    duration = 200.ms,
                    timingFunction = TransitionTimingFunction.Ease
                ), // Transition for background-color
                Transition.of(
                    property = "box-shadow",
                    duration = 200.ms,
                    TransitionTimingFunction.Ease
                ),             // Transition for width

            )
            .fillMaxWidth(),
        horizontalAlignment = Alignment.CenterHorizontally
    ) {
        // Product Image
        Image(
            src = product.imageUrl,
            modifier = Modifier
                .fillMaxWidth()
                .height(200.px)
                .borderRadius(0.5.cssRem)
                .objectFit(ObjectFit.Cover)
                .margin(bottom = 1.cssRem)
        )

        // Product Name
        SpanText(
            product.name,
            Modifier
                .fontSize(1.5.cssRem)
                .fontWeight(FontWeight.Bold)
                .margin(bottom = 0.5.cssRem)
        )

        // Product Description
        SpanText(
            product.description,
            Modifier
                .fontSize(0.9.cssRem)
                .color(org.jetbrains.compose.web.css.rgba(100, 100, 100, 0.8))
                .margin(bottom = 1.cssRem)
                .textAlign(TextAlign.Center)
        )

        // Product Price and Buy Button
        Column(
            modifier = Modifier.fillMaxWidth(),
            horizontalAlignment = Alignment.End,
            verticalArrangement = Arrangement.Bottom,
//            horizontalAlignment = Alignment.CenterVertically
        ) {
            SpanText(
                "$${product.price}",
                Modifier
                    .fontSize(1.8.cssRem)
                    .fontWeight(FontWeight.Bold)
                    .color(ColorMode.current.toSitePalette().brand.accent)
            )

            Spacer()

            // Buy Button
            Box(
                modifier = Modifier
                    .padding(0.75.cssRem, 0.5.cssRem)
                    .borderRadius(0.5.cssRem)
                    .backgroundColor(ColorMode.current.toSitePalette().brand.accent)
                    .cursor(Cursor.Pointer)
                    .onClick { event ->
                        event.stopPropagation()
                        onEvent(ProductsUiEvent.AddToCart(product))
                    },
                contentAlignment = Alignment.Center
            ) {
                Row(
                    modifier = Modifier.gap(0.5.cssRem),
                    verticalAlignment = Alignment.CenterVertically
                ) {
                    FaCartPlus(
                        modifier = Modifier
                            .fontSize(1.2.cssRem)
                            .color(org.jetbrains.compose.web.css.rgb(255, 255, 255))
                    )
                    SpanText(
                        "Agregar",
                        Modifier
                            .fontSize(1.cssRem)
                            .fontWeight(FontWeight.Bold)
                            .color(org.jetbrains.compose.web.css.rgb(255, 255, 255))
                    )
                }
            }
        }
    }
}

val ProductsGridStyle = CssStyle {
    base {
        Modifier
            .fillMaxWidth()
            .gap(2.cssRem)
            .display(org.jetbrains.compose.web.css.DisplayStyle.Grid)
            .gridTemplateColumns { repeat(1) { size(1.fr) } }
    }
    Breakpoint.MD {
        Modifier.gridTemplateColumns { repeat(2) { size(1.fr) } }
    }
    Breakpoint.LG {
        Modifier.gridTemplateColumns { repeat(3) { size(1.fr) } }
    }
}
