package mzx.ventas.ui.products

import kotlinx.coroutines.CoroutineScope
import kotlinx.coroutines.MainScope
import kotlinx.coroutines.cancel
import kotlinx.coroutines.flow.*
import kotlinx.coroutines.launch
import mzx.ventas.data.element.ProductItemElement
import mzx.ventas.domain.usecase.CartUpdaterUseCase
import mzx.ventas.domain.usecase.GetProductsUseCase
import mzx.ventas.ui.Lce

class ProductsViewModel constructor(
    private val getProductsUseCase: GetProductsUseCase,
    private val cartUpdaterUseCase: CartUpdaterUseCase
) :
    CoroutineScope by MainScope() {

    private val _state: MutableStateFlow<Lce<Exception, ProductsUiState>> = MutableStateFlow(
        Lce.Loading
    )
    val state = _state.asStateFlow()

    private val _navigationEffect = MutableSharedFlow<ProductsNavigationEffect>()
    val navigationEffect = _navigationEffect.asSharedFlow()

    fun onEvent(event: ProductsUiEvent) {
        when (event) {
            ProductsUiEvent.Init -> launch {
                val result: List<ProductItemElement> = getProductsUseCase.action()
                _state.update {
                    Lce.Content(ProductsUiState(result))
                }
            }
            is ProductsUiEvent.NavigateToProductDetail -> launch {
                _navigationEffect.emit(ProductsNavigationEffect.NavigateToDetail(event.productId))
            }
            is ProductsUiEvent.AddToCart -> {
                launch { cartUpdaterUseCase.action(CartUpdaterUseCase.CartUpdaterActions.Add(event.product)) }
            }
        }
    }

    fun onDispose() {
        cancel() // cancels all coroutines
    }
}

data class ProductsUiState(val items: List<ProductItemElement>) {
    companion object {
        val EMPTY = ProductsUiState(emptyList())
    }
}

sealed interface ProductsUiEvent {
    object Init : ProductsUiEvent
    data class NavigateToProductDetail(val productId: String) : ProductsUiEvent
    data class AddToCart(val product: ProductItemElement) : ProductsUiEvent
}

sealed interface ProductsNavigationEffect {
    data class NavigateToDetail(val productId: String) : ProductsNavigationEffect
}
