package mzx.ventas.data.repository

import kotlinx.coroutines.channels.Channel
import kotlinx.coroutines.flow.Flow
import kotlinx.coroutines.flow.flow
import kotlinx.coroutines.sync.Mutex
import kotlinx.coroutines.sync.withLock
import mzx.ventas.data.element.ProductItemElement
import mzx.ventas.service.CartService

interface CartRepository {
    suspend fun addProductToCart(productItemElement: ProductItemElement)

    fun cartState(): Flow<CartState>
}

class CartRepositoryImpl(private val cartService: CartService) : CartRepository {

    private var cart =  cartService.fetchCartState()
    private val mutex = Mutex()

    private val channel: Channel<CartState> = Channel(capacity = 1)
    override suspend fun addProductToCart(productItemElement: ProductItemElement) {

        mutex.withLock {
            cart = cart.addProduct(productItemElement)
            cart
        }.let {
            console.log("Product Added2 : $productItemElement")
            channel.send(it)
            cartService.updateCartState(cart)
        }
    }

    @OptIn(ExperimentalWasmJsInterop::class)
    override fun cartState(): Flow<CartState> = flow {
        emit(cart)
        do {
            val item = channel.receive()
            emit(item)
        } while (true)
    }
}

data class CartState(val products: Map<String, Int>) {
    companion object {
        val EMPTY = CartState(emptyMap())
    }
}

private fun CartState.addProduct(productItemElement: ProductItemElement): CartState {
    val count = (this.products[productItemElement.id] ?: 0) + 1
    console.log("Old Print $this")
    return copy(products = products + (productItemElement.id to count)).also {
        console.log("New Print $this")
    }
}
