package mzx.ventas.pages

import androidx.compose.runtime.Composable
import com.varabyte.kobweb.core.Page
import com.varabyte.kobweb.core.data.add
import com.varabyte.kobweb.core.init.InitRoute
import com.varabyte.kobweb.core.init.InitRouteContext
import com.varabyte.kobweb.core.layout.Layout
import com.varabyte.kobweb.core.rememberPageContext
import mzx.ventas.components.layouts.PageLayoutData
import mzx.ventas.ui.productdetail.ProductDetail
import mzx.ventas.ui.productdetail.decodeURIComponent

@InitRoute
fun initProductDetailPage(ctx: InitRouteContext) {
    ctx.data.add(PageLayoutData("Product Details"))
}

@Page("/products/{productId}")
@Layout(".components.layouts.PageLayout")
@Composable
fun ProductDetailPage() {
    val ctx = rememberPageContext()
    val productId = ctx.route.params["productId"]?.let {
        decodeURIComponent(it)
    } ?: ""
    ProductDetail(productId)
}