package mzx.ventas.data.repository

import mzx.ventas.data.element.CategoryElement
import mzx.ventas.data.element.ProductItemElement
import mzx.ventas.service.CategoryData
import mzx.ventas.service.ProductItemData
import mzx.ventas.service.LocalReaderService


interface ProductsRepository {
    suspend fun load(): List<ProductItemElement>
}

class ProductsRepositoryImp constructor(private val localReaderService: LocalReaderService) : ProductsRepository {

    override suspend fun load(): List<ProductItemElement> {
        return localReaderService.fetchAllProducts().map { mapper(it) }
    }

    private suspend fun mapper(data: ProductItemData): ProductItemElement =
        ProductItemElement(
            id = data.id,
            name = data.name,
            price = data.price,
            imageUrl = data.imageUrl,
            description = data.description,
            categoryElement = mapper(data.categoryIds),
            sellType = data.sellType
        )

    private suspend fun mapper(categoryIds: List<String>): List<CategoryElement> =
        localReaderService.fetchCategories(categoryIds).map(::mapper)

    private fun mapper(data: CategoryData): CategoryElement =
        CategoryElement(data.id, data.name, data.description, data.benefits)
}

