package mzx.ventas.domain.usecase

import mzx.ventas.data.element.ProductItemElement
import mzx.ventas.data.repository.ProductsRepository
import mzx.ventas.service.ProductItemData

interface GetProductsUseCase {
    suspend fun action(productFilter: ProductFilter= ProductFilter.ALL):List<ProductItemElement>
}

class GetProductsUseCaseImpl constructor(val productsRepository: ProductsRepository): GetProductsUseCase {
    override suspend fun action(productFilter: ProductFilter): List<ProductItemElement> {
        return productsRepository.load()
    }
}

enum class ProductFilter {
    ALL
}

