package mzx.ventas.domain.usecase

import mzx.ventas.data.element.ProductItemElement
import mzx.ventas.data.repository.ProductsRepository
import mzx.ventas.service.ProductItemData

interface GetProductByNameUseCase {
    suspend fun action(productId: String): ProductItemElement?
}

class GetProductByNameUseCaseImpl constructor(val productsRepository: ProductsRepository): GetProductByNameUseCase {
    override suspend fun action(productId: String): ProductItemElement? {
        return productsRepository.load().find { it.id == productId }
    }
}
