package mzx.ventas.ui.productdetail

import kotlinx.coroutines.CoroutineScope
import kotlinx.coroutines.MainScope
import kotlinx.coroutines.cancel
import kotlinx.coroutines.flow.MutableStateFlow
import kotlinx.coroutines.flow.asStateFlow
import kotlinx.coroutines.launch
import mzx.ventas.data.element.ProductItemElement
import mzx.ventas.domain.usecase.GetProductByNameUseCase

class ProductDetailViewModel constructor(
    private val getProductByNameUseCase: GetProductByNameUseCase
) : CoroutineScope by MainScope() {

    private val _state: MutableStateFlow<ProductDetailUiState> = MutableStateFlow(ProductDetailUiState.Loading)
    val state = _state.asStateFlow()

    fun onEvent(event: ProductDetailUiEvent) {
        when (event) {
            is ProductDetailUiEvent.LoadProduct -> launch {
                _state.value = ProductDetailUiState.Loading
                _state.value =
                    getProductByNameUseCase.action(event.productId)?.let { ProductDetailUiState.Success(it) }
                        ?: ProductDetailUiState.NotFound
            }
        }
    }

    fun onDispose() {
        cancel()
    }
}

sealed interface ProductDetailUiState {
    data object Loading : ProductDetailUiState
    data class Success(val product: ProductItemElement) : ProductDetailUiState
    data object NotFound : ProductDetailUiState
}

sealed interface ProductDetailUiEvent {
    data class LoadProduct(val productId: String) : ProductDetailUiEvent
}
