package mzx.ventas.ui.cart

import kotlinx.coroutines.CoroutineScope
import kotlinx.coroutines.MainScope
import kotlinx.coroutines.cancel
import kotlinx.coroutines.flow.*
import kotlinx.coroutines.launch
import mzx.ventas.domain.usecase.GetCartStatUseCase

class CartStateViewModel(private val getCartStatUSeCase: GetCartStatUseCase) : CoroutineScope by MainScope() {

    private val _state = MutableStateFlow(CartState(0))
    val state = _state.asStateFlow()

    private val _effect = MutableSharedFlow<CartStateEffect>()
    val effect = _effect.asSharedFlow()

    fun onEvent(event: CartStateEvent) {
        when (event) {
            CartStateEvent.Init -> {
                launch {
                    getCartStatUSeCase.action().collect { state ->
                        _state.update { it.copy(itemsCount = state.products.count()) }
                    }
                }
            }
        }
    }

    fun onDispose() {
        cancel()
    }
}

data class CartState(val itemsCount: Int)

interface CartStateEvent {
    object Init : CartStateEvent
}

interface CartStateEffect
