package mzx.ventas.ui.cart

import kotlinx.coroutines.CoroutineScope
import kotlinx.coroutines.MainScope
import kotlinx.coroutines.cancel
import kotlinx.coroutines.flow.MutableSharedFlow
import kotlinx.coroutines.flow.MutableStateFlow
import kotlinx.coroutines.flow.asSharedFlow
import kotlinx.coroutines.flow.asStateFlow
import kotlinx.coroutines.flow.update
import kotlinx.coroutines.launch
import mzx.ventas.data.element.ProductItemElement

class CartViewModel : CoroutineScope by MainScope() {

    private val _state = MutableStateFlow(CartUiState.EMPTY)
    val state = _state.asStateFlow()

    private val _effect = MutableSharedFlow<CartEffect>()
    val effect = _effect.asSharedFlow()

    fun onEvent(event: CartUiEvent) {
        when (event) {
            is CartUiEvent.AddItem -> {
                addOrUpdateItem(event.product, event.quantity)
            }
            is CartUiEvent.AddItemWithAmount -> {
//                addOrUpdateItemWithAmount(event.product, event.amount)
            }
            is CartUiEvent.RemoveItem -> {
                removeItem(event.productId)
            }
            is CartUiEvent.UpdateQuantity -> {
                updateQuantity(event.productId, event.quantity)
            }
            is CartUiEvent.UpdateAmount -> {
//                updateAmount(event.productId, event.amount)
            }
            is CartUiEvent.IncrementQuantity -> {
                incrementQuantity(event.productId)
            }
            is CartUiEvent.DecrementQuantity -> {
                decrementQuantity(event.productId)
            }
            is CartUiEvent.IncrementAmount -> {
//                incrementAmount(event.productId, event.step)
            }
            is CartUiEvent.DecrementAmount -> {
//                decrementAmount(event.productId, event.step)
            }
            CartUiEvent.ClearCart -> {
                clearCart()
            }
        }
    }

    private fun addOrUpdateItem(product: ProductItemElement, quantityToAdd: Int) {
        _state.update { currentState ->
            val existingItem = currentState.items.find { it.product.id == product.id }

            val updatedItems = if (existingItem != null) {
                // Update existing item quantity
                currentState.items.map { item ->
                    if (item.product.id == product.id) {
                        item.copy(quantity = item.quantity + quantityToAdd)
                    } else {
                        item
                    }
                }
            } else {
                // Add new item
                currentState.items + CartItem(product, quantityToAdd)
            }

            currentState.copy(items = updatedItems)
        }

        // Emit success effect
        launch {
            _effect.emit(CartEffect.ItemAdded(product.name))
        }
    }

    private fun removeItem(productId: String) {
        _state.update { currentState ->
            val updatedItems = currentState.items.filter { it.product.id != productId }
            currentState.copy(items = updatedItems)
        }

        launch {
            _effect.emit(CartEffect.ItemRemoved)
        }
    }

    private fun updateQuantity(productId: String, newQuantity: Int) {
        if (newQuantity <= 0) {
            removeItem(productId)
            return
        }

        _state.update { currentState ->
            val updatedItems = currentState.items.map { item ->
                if (item.product.id == productId) {
                    item.copy(quantity = newQuantity)
                } else {
                    item
                }
            }
            currentState.copy(items = updatedItems)
        }
    }

    private fun incrementQuantity(productId: String) {
        _state.update { currentState ->
            val updatedItems = currentState.items.map { item ->
                if (item.product.id == productId) {
                    item.copy(quantity = item.quantity + 1)
                } else {
                    item
                }
            }
            currentState.copy(items = updatedItems)
        }
    }

    private fun decrementQuantity(productId: String) {
        _state.update { currentState ->
            val itemToDecrement = currentState.items.find { it.product.id == productId }

            if (itemToDecrement != null && itemToDecrement.quantity <= 1) {
                // Remove item if quantity would become 0
                removeItem(productId)
                return@update currentState
            }

            val updatedItems = currentState.items.map { item ->
                if (item.product.id == productId) {
                    item.copy(quantity = item.quantity - 1)
                } else {
                    item
                }
            }
            currentState.copy(items = updatedItems)
        }
    }

    private fun clearCart() {
        _state.update { CartUiState.EMPTY }

        launch {
            _effect.emit(CartEffect.CartCleared)
        }
    }

    fun onDispose() {
        cancel()
    }
}

/**
 * Represents a single item in the shopping cart
 */
data class CartItem(
    val product: ProductItemElement,
    val quantity: Int,
    val amount: Double? = null  // For weight/volume-based items (kg, liters, etc.)
) {
    val subtotal: Double
        get() {
            return when (product.sellType) {
                mzx.ventas.service.SellType.Unit -> {
                    // For unit-based items, use quantity
                    product.price * quantity
                }
                mzx.ventas.service.SellType.Kilogram,
                mzx.ventas.service.SellType.G100,
                mzx.ventas.service.SellType.Liter,
                mzx.ventas.service.SellType.Other -> {
                    // For weight/volume-based items, use amount if provided, otherwise default to quantity
                    product.price * (amount ?: quantity.toDouble())
                }
            }
        }

    /**
     * Returns the display quantity/amount for this item
     */
    val displayAmount: String
        get() {
            return when (product.sellType) {
                mzx.ventas.service.SellType.Unit -> "$quantity"
                mzx.ventas.service.SellType.Kilogram -> "${amount ?: quantity.toDouble()} kg"
                mzx.ventas.service.SellType.G100 -> "${(amount ?: quantity.toDouble()) * 100} g"
                mzx.ventas.service.SellType.Liter -> "${amount ?: quantity.toDouble()} L"
                mzx.ventas.service.SellType.Other -> "${amount ?: quantity.toDouble()}"
            }
        }
}

/**
 * UI state for the shopping cart
 */
data class CartUiState(
    val items: List<CartItem>
) {
    val totalItems: Int
        get() = items.sumOf { it.quantity }

    val totalPrice: Double
        get() = items.sumOf { it.subtotal }

    val isEmpty: Boolean
        get() = items.isEmpty()

    companion object {
        val EMPTY = CartUiState(emptyList())
    }
}

/**
 * Events that can be triggered on the cart
 */
sealed interface CartUiEvent {
    /**
     * Add a product to cart or increase its quantity if already exists
     * For unit-based items, use quantity
     */
    data class AddItem(val product: ProductItemElement, val quantity: Int = 1) : CartUiEvent

    /**
     * Add a weight/volume-based product to cart with a specific amount
     * For weight/volume-based items (kg, liters, etc.)
     */
    data class AddItemWithAmount(val product: ProductItemElement, val amount: Double) : CartUiEvent

    /**
     * Remove a product completely from cart
     */
    data class RemoveItem(val productId: String) : CartUiEvent

    /**
     * Update the quantity of a product in cart (for unit-based items)
     */
    data class UpdateQuantity(val productId: String, val quantity: Int) : CartUiEvent

    /**
     * Update the amount of a weight/volume-based product in cart
     */
    data class UpdateAmount(val productId: String, val amount: Double) : CartUiEvent

    /**
     * Increment quantity by 1 (for unit-based items)
     */
    data class IncrementQuantity(val productId: String) : CartUiEvent

    /**
     * Decrement quantity by 1 (removes if would become 0, for unit-based items)
     */
    data class DecrementQuantity(val productId: String) : CartUiEvent

    /**
     * Increment amount by a step (for weight/volume-based items)
     */
    data class IncrementAmount(val productId: String, val step: Double = 0.1) : CartUiEvent

    /**
     * Decrement amount by a step (removes if would become <= 0, for weight/volume-based items)
     */
    data class DecrementAmount(val productId: String, val step: Double = 0.1) : CartUiEvent

    /**
     * Remove all items from cart
     */
    object ClearCart : CartUiEvent
}

/**
 * Side effects from cart operations
 */
sealed interface CartEffect {
    data class ItemAdded(val productName: String) : CartEffect
    object ItemRemoved : CartEffect
    object CartCleared : CartEffect
}
