package mzx.ventas.service

import CookieOptions
import com.varabyte.kobweb.browser.storage.StorageKey
import com.varabyte.kobweb.browser.storage.getItem
import com.varabyte.kobweb.browser.storage.setItem
import kotlinx.browser.localStorage
import mzx.ventas.data.repository.CartState

class CartServiceImpl : CartService {

    private val storageKey = CartStateStorageKey(CART_SESSION)

    //       / 1. Initialize as a plain JS object
    private val options = js("{}").unsafeCast<CookieOptions>().apply {
        expires = 7
        path = "/"
        secure = true
        sameSite = "strict"
    }


    override fun updateCartState(cart: CartState) {
        localStorage.setItem(storageKey, cart)
    }

    override fun fetchCartState(): CartState {
        return localStorage.getItem(storageKey) ?: throw IllegalArgumentException()
    }

    companion object {
        const val CART_SESSION = "cart_session_store"
    }
}

class CartStateStorageKey(name: String, defaultValue: CartState = CartState.EMPTY) :
    StorageKey<CartState>(name, defaultValue) {
    override fun convertToString(value: CartState): String =
        value.products.toList().map { "${it.first},${it.second}" }.joinToString(separator = ";") { it }

    override fun convertFromString(value: String): CartState =
        value.split(";").associate { value ->
            val element = value.split(",")
            element[0] to element[1].toInt()
        }.let { CartState(it) }
}