package mzx.ventas.service

import arrow.core.Option
import arrow.core.none
import arrow.core.some
import com.varabyte.kobweb.core.AppGlobals
import kotlinx.browser.window
import kotlinx.coroutines.await
import kotlinx.serialization.json.Json

class LocalReaderServiceImp : LocalReaderService {

    private var categories: Option<List<CategoryData>> = none()

    override suspend fun fetchAllProducts(): List<ProductItemData> = parseJsonData(fileName = "data")

    override suspend fun fetchAllCategories(): List<CategoryData> = categories.fold({
        val categories: List<CategoryData> = parseJsonData(fileName = "category")
        this.categories = categories.some()
        categories
    }, {
        it
    })

    override suspend fun fetchCategories(categoryIds: List<String>): List<CategoryData> =
        fetchAllCategories().filter { it.id in categoryIds }

}

suspend inline fun <reified T> parseJsonData(fileName: String) =
    try {
        Json.decodeFromString<T>(readJsonFile(fileName = fileName))
    } catch (e: Exception) {
        console.log(e.stackTraceToString())
        throw e
    }


suspend fun readJsonFile(fileName: String): String {
    val response =
        try {
            window.fetch("/${AppGlobals.basePath()}/data/$fileName.json")
        } catch (e: Exception) {
            console.log(e.stackTraceToString())
            throw e
        }.await()

    return response.text().await()
}


private fun AppGlobals.basePath(): String = try {
    getValue("basePath")
} catch (e: Exception) {
//    console.log(e.stackTraceToString())
    "saboresapp.github.io"
}