package mzx.ventas.service

import kotlinx.serialization.SerialName
import kotlinx.serialization.Serializable

@Serializable
data class ProductItemData(
    val id: String,
    val name: String,
    val price: Double,
    val imageUrl: String,
    val description: String,
    val longDescription: String,
    val categoryIds: List<String>,
    val sellType: SellType
)

@Serializable
enum class SellType {
    @SerialName("KILOGRAM")
    Kilogram,

    @SerialName("GRAMS_100")
    G100,

    @SerialName("LITER")
    Liter,

    @SerialName("UNIT")
    Unit,

    @SerialName("OTHER")
    Other,
}