package mzx.ventas.domain.usecase

import mzx.ventas.data.element.ProductItemElement
import mzx.ventas.data.repository.CartRepository

interface CartUpdaterUseCase {
    val cartRepository: CartRepository
    suspend fun action(actions: CartUpdaterActions) {
        when (actions) {
            is CartUpdaterActions.Add -> {
                cartRepository.addProductToCart(actions.productItemElement)
            }
        }
    }

    interface CartUpdaterActions {
        data class Add(val productItemElement: ProductItemElement) : CartUpdaterActions
    }
}

class CartUpdaterUseCaseImpl(override val cartRepository: CartRepository) : CartUpdaterUseCase
